* {
  box-sizing: border-box;
  font-size: 16px;
  margin: 0;
  padding: 0;
  font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}

:root {
  --main-border-color: var(--gray-100); /*#f0f0f0*/
  --point-color: var(--coz-purple-600); /*#452cdd*/
  --point-color-tint: var(--coz-purple-400); /*#a296ee*/
  --point-color-tint-2: var(--coz-purple-200); /*#d8d3f8*/
  --default-text-color: var(--gray-900); /*#141414*/
  --sub-text-color: var(--gray-700); /*#5c5c5c*/
  --bold-text-color: black;
  --button-text-color: white;
  --shade-color: var(--gray-200); /*#e6e6e6*/
  --sidebar-component-color: #ff577f;
  --count-component-color: #ffc764;
  --footer-component-color: #ff884b;
  --tweets-component-color: #cdfffc;
  --tweet-component-color: #007580;
}

body {
  display: flex;
  justify-content: center;
  min-height: 100vh;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit; /* 1 */
  font-size: 100%; /* 1 */
  line-height: 1.15; /* 1 */
  margin: 0; /* 2 */
  border: 0;
}

button,
input {
  overflow: visible; /* 1 */
}

button {
  color: var(--button-text-color);
}

button:hover,
button:focus {
  background: var(--point-color);
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  transform: scale(0.99);
}

textarea {
  overflow: auto;
  resize: none;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

footer {
  position: relative;
  flex: 0 0 1rem;
  padding: 0.3rem;
  font-size: 14px;
  font-weight: 400;
  color: var(--gray-600);
  text-align: right;
  border: 2px solid var(--footer-component-color);
}

main {
  display: flex;
  flex-direction: row;
  max-width: 640px;
  flex: 1 0 0;
}

.sidebar {
  display: flex;
  flex-direction: column;
  border: 2px solid var(--sidebar-component-color);
}

.far,
.fas {
  font-size: 2rem;
  color: var(--default-text-color);
  padding: 1.3rem;
  cursor: pointer;
}

.features {
  display: flex;
  flex-direction: column;
  border: 1px solid var(--main-border-color);
  flex: 1 0 0;
}

.tweetForm__container,
.notificationBar__container {
  border: 2px solid var(--count-component-color);
  margin-bottom: 0.5rem;
}

.tweetForm__wrapper,
.notificationBar__wrapper {
  display: flex;
  flex-direction: row;
  padding: 0.5rem 1rem;
}

.tweetForm__profile,
.notificationBar__icon {
  padding: 0.2rem;
  flex: 0 0 0;
}

.tweetForm__profile > img {
  display: inline-block;
  width: 50px;
  border-radius: 100%;
}

.tweetForm__input,
.notificationBar__message {
  display: flex;
  justify-content: center;
  align-content: center;
  flex-direction: column;
  flex: 1 0 0;
}

.tweetForm__inputWrapper {
  display: flex;
  margin: 0.5rem;
  flex: 0 0 4rem;
}

.tweetForm__textarea {
  flex: 1 0 0;
  border: 0;
}

.tweetForm__count {
  margin: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tweetForm__count__text {
  color: var(--sub-text-color);
}

.tweetForm__submit {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex: 1 0 0;
}

.tweetForm__submitIcons {
  flex: 1 0 0;
}

.tweetForm__submitButton {
  background-color: var(--point-color-tint);
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 1rem;
}

.tweets,
.notifications {
  flex: 1 0 80vh;
  overflow-y: scroll;
  border: 2px solid var(--tweets-component-color);
}

.tweet,
.notification {
  display: flex;
  list-style: none;
  padding: 0.5rem 1rem;
  border: 2px solid var(--tweet-component-color);
}

.tweet:hover,
.notification:hover {
  background-color: var(--shade-color);
}

.tweet__profile,
.notification__sign {
  padding: 0.2rem;
}

.tweet__profile > img {
  display: inline-block;
  width: 50px;
  border-radius: 100%;
}

.notification__sign > .fas {
  display: initial;
  cursor: initial;
}

.tweet__content .notification__content {
  flex: 1 0 0;

}

.tweet__userInfo > .tweet__username {
  color: var(--bold-text-color);
  font-weight: 700;
  margin-left: 0.5rem;
}

.tweet__userInfo > .tweet__username.tweet__username--purple {
  background-color: var(--point-color-tint-2);
}

.tweet__userInfo > .tweet__createdAt {
  color: var(--sub-text-color);
  margin-left: 0.5rem;
}

.tweet__message {
  padding: 0.5rem;
}

#logo {
  position: absolute;
  left: 12px;
  top: 4px;
  height: 22px;
}
